<template>
    <div class="panel">
        <div class="panel-header">
            <div>
                <small>Aktualny rozmiar: {{stats.size}} mb</small>
                <h3>Akcje na bazie danych {{stats.name}} </h3>
            </div>
        </div>
        <div class="db-action-container">
            <button @click="runDatabaseCopy">Zrzut bazy</button>
            <button @click="deleteRows">Usuń rekordy</button>
            <button @click="deleteTables">Usuń tabele</button>
            <button @click="runDatabaseMigrations">Migracja tabel</button>
            <button @click="runDatabaseMigrations(true)">Domigruj tabele</button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "TableActionPanel",
        props: [
            "stats"
        ],
        methods:{
            deleteRows(){
                axios.delete(`/database/row`)
                    .then((response) => {
                        this.$emit('refresh');
                    });
            },
            deleteTables(){
                axios.delete(`/database/table`)
                    .then((response) => {
                        this.$emit('refresh');
                    });
            },
            runDatabaseMigrations(onlyNonExist = false){

                if(onlyNonExist) onlyNonExist = '?onlyNonExist=true';
                else onlyNonExist = '';

                axios.post(`/database/migration${onlyNonExist}`)
                    .then((response) => {
                        this.$emit('refresh');
                    });
            },
            runDatabaseCopy(){
                axios.get(`/database/copy`,{responseType: 'blob'})
                    .then((response) => {
                        this.$emit('refresh');
                    });
            }
        }
    }
</script>

<style scoped>
    .db-action-container{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
    }

</style>


