<template>
    <main>
        <div class="row">
            <div class="panel col wow fadeInUp" style="max-width: 500px; margin: auto">
                <div class="panel-header">
                    <h3>Dodawanie nowego uprawnienia</h3>
                </div>
                <form>

                    <label for="permissionName">Nazwa uprawnienia</label>
                    <input type="text" id="permissionName" v-model="permissionName" autofocus>

                    <label for="rolePermission">Kto ma posiadać to uprawnienie?</label>
                    <table id='rolePermission' class="sortable-theme-light" data-sortable>
                        <thead>
                        <tr>
                            <th>Nazwa</th>
                            <th>Zezwolić?</th>
                        </tr>
                        </thead>
                        <tbody>
                        <tr v-for="role in rolePermission">
                            <td>{{role.name}}</td>
                            <td>
                                <input type="checkbox" v-model="role.permission">
                            </td>
                        </tr>
                        </tbody>
                    </table>

                    <button v-if="!isEdit" @click="addPermission" :disabled="permissionName.length < 3">Dodaj</button>
                    <button v-else @click="savePermission" :disabled="permissionName.length < 3">Zapisz</button>
                </form>
            </div>
        </div>
    </main>
</template>

<script>
    export default {
        name: "PermissionAdd",
        props: ['id'],
        data() {
            return {
                permissionName: '',
                isEdit: this.id !== 'add',
                rolePermission: []
            }
        },
        methods: {
            validForm() {
                try {
                    let valid = new window.Validator({
                        "nazwa uprawnienia": this.permissionName,
                    });
                    valid.get("nazwa uprawnienia").length(3, 50);
                } catch (e) {
                    return false;
                }
                return true;
            },
            addPermission() {
                if (!this.validForm()) return;

                axios.post(`/permission`, {
                    name: this.permissionName,
                    permission: this.rolePermission
                })
                    .then((response) => {
                        this.$router.push('permission');
                    })
            },
            savePermission() {
                if (!this.validForm()) return;

                axios.put(`/permission/${this.id}`, {
                    name: this.permissionName,
                    permission: this.rolePermission
                })
                    .then((response) => {
                        this.$router.push('permission');
                    })
            },
            isRolePerm(roleId, rolePermArray){
                for(let i=0;i<rolePermArray.length;i++){
                    if(rolePermArray[i].role_id === roleId) return true;
                }
                return false;
            }
        },
        mounted() {

            axios.get(`/role`)
                .then((response) => {
                    let roles = response.data;
                    this.rolePermission = [];

                    if(!this.isEdit){
                        for(let i=0;i<response.data.length;i++){
                            this.rolePermission.push({
                                id: response.data[i].id,
                                name: response.data[i].name,
                                permission: false
                            });
                        }
                    }

                    if (this.isEdit) {
                        axios.get(`/permission/${this.id}`)
                            .then((response) => {
                                this.permissionName = response.data.permission.name;

                                for(let i=0;i<roles.length;i++){
                                    this.rolePermission.push({
                                        id: roles[i].id,
                                        name: roles[i].name,
                                        permission: this.isRolePerm(roles[i].id,response.data.rolePermission)
                                    });
                                }
                            })
                    }

                });

        }
    }
</script>

<style scoped>

</style>


