<template>
    <div class="panel">
        <div class="panel-header">
            <h3>Role na stronie</h3>
            <button class="panelActionTop" @click="$router.push('/role/add')">Dodaj</button>
        </div>
        <table class="sortable-theme-light" data-sortable>
            <thead>
            <tr>
                <th>ID</th>
                <th>Nazwa</th>
                <th>Akcje</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="role in roles">
                <td>{{role.id}}</td>
                <td>{{role.name}}</td>
                <td class="action">
                    <div @click='editRole(role.id)'>
                        <i class="btn-i fas fa-edit"></i>
                    </div>
                    <div @click='deleteRole(role.id)'>
                        <i class="btn-i fas fa-trash"></i>
                    </div>
                </td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    export default {
        name: "RolePanel",
        data: () => (
            {
                roles: []
            }
        ),
        methods: {
            getRoles(){
                axios.get(`/role`)
                    .then((response) => {
                        this.roles = response.data;
                    })
            },
            editRole(id){
                this.$router.push(`/role/${id}`);
            },
            deleteRole(id){
                axios.delete(`/role/${id}`)
                    .then((response) => {
                        this.$emit('refresh');
                        this.getRoles();
                    })
            }
        },
        mounted: function(){
            this.getRoles();
        }
    }
</script>

<style scoped>

</style>


