<template>
    <main>
        <div class="row">
            <div class="panel col wow fadeInUp" style="max-width: 500px; margin: auto">
                <div class="panel-header">
                    <h3>Dodawanie nowego użytkownika</h3>
                </div>
                <form>

                    <label for="userFirstname">Imię</label>
                    <input type="text" id="userFirstname" v-model="user.firstname" autofocus>

                    <label for="userLastname">Nazwisko</label>
                    <input type="text" id="userLastname" v-model="user.lastname">

                    <label for="userEmail">Adres email</label>
                    <input type="email" id="userEmail" v-model="user.email">

                    <label for="userPassword">Hasło</label>
                    <input type="password" id="userPassword" v-model="user.password">

                    <label for="userPasswordRep">Powtórz hasło</label>
                    <input type="password" id="userPasswordRep" v-model="user.passwordRep">

                    <label for="userRole">Rola użytkownika</label>
                    <select id="userRole" v-model="user.role_id" style="margin-bottom: 15px;">
                        <option v-for="role in roles" :value="role.id">{{role.name}}</option>
                    </select>

                    <form v-show="user.role_id && user.role_id.length > 0" id="roleForm">
                        <p>Ładowanie...</p>
                    </form>

                    <button v-if="!isEdit" @click="addUser">Dodaj</button>
                    <button v-else @click="saveUser">Zapisz</button>
                </form>
            </div>
        </div>
    </main>
</template>

<script>
    export default {
        name: "UserAdd",
        props: ['id'],
        data() {
            return {
                role: null,
                roles: [],
                user: {
                    firstname: "",
                    lastname: "",
                    email: "",
                    password: "",
                    passwordRep: "",
                    role_id: ""
                },
                isEdit: this.id !== 'add'
            }
        },
        methods: {
            generateRoleFormValue() {
                let valueArray = [];

                $('#roleForm').find('input').each( function () {
                    if ($(this).val()) {
                        let inputObj = {
                            field_id: $(this).data('id'),
                            value: $(this).val()
                        };

                        if($(this).attr('type') === 'file'){
                            inputObj.value = $(this).data('value');
                        }

                        valueArray.push(inputObj);

                    }
                });

                return valueArray;
            },
            validateForm(errorShow = true) {
                try {
                    let valid = new window.Validator({
                        "imię": this.user.firstname,
                        "nazwisko": this.user.lastname,
                        "email": this.user.email,
                        "hasło": this.user.password,
                        "nazwa roli": this.user.role_id,
                    }, errorShow);
                    valid.get("imię").length(3, 50);
                    valid.get("nazwisko").length(3, 50);
                    valid.get("email").length(3, 50);
                    valid.get("hasło").length(5, 20);
                    valid.get("nazwa roli").length(1, 10);

                    if (this.user.password !== this.user.passwordRep) {
                        notify.createNotifier("Hasła nie pasują do siebie", "danger", 4000);
                        throw "validate failed";
                    }

                    let isValid = true;

                    $('#roleForm').find('input').each(function () {

                        if ($(this).prop('required')) {
                            if ($(this).val()) {
                                $(this).removeClass('badReq');
                            } else {
                                $(this).addClass('badReq');
                                isValid = false;
                            }
                        }
                    });

                    if (!isValid) {
                        notify.createNotifier("Uzupełnij formularz roli", "danger", 4000);
                        throw "validate failed";
                    }

                } catch (e) {
                    return false;
                }
                return true;
            },
            getUser() {
                let self = this;
                axios.get(`/user/${this.id}`)
                    .then((response) => {
                        self.user = response.data;
                        axios.get(`/role/${self.user.role_id}`)
                            .then((response) => {
                                self.role = response.data;
                                self.buildRoleForm(self.user.formField);
                            })
                    })
            },
            addUser() {
                if (!this.validateForm()) return;

                this.user.roleForm = this.generateRoleFormValue();

                axios.post(`/user`, this.user)
                    .then((response) => {
                        this.$router.push('/user');
                    })
            },
            saveUser() {
                if (!this.validateForm()) return;

                this.user.roleForm = this.generateRoleFormValue();

                axios.put(`/user/${this.id}`, this.user)
                    .then((response) => {
                       this.$router.push('/user');
                    })
            },
            buildRoleForm( fieldsValue = []) {
                let html = '';

                for (let i = 0; i < this.role.form_field.length; i++) {
                    let field = this.role.form_field[i];

                    let type = field.field_type;
                    let id = field.id;
                    let label = field.label;
                    let listField = field.list_field;
                    let isRequired = field.is_required === '0' ? '' : 'required';

                    if (this.isEdit && field.value && field.value.length >= 1) {
                        isRequired = '';
                    }

                    let labelHtml = `<label for='role_field_${id}'>${label}</label>`;
                    let inputHtml = '';

                    switch (type) {
                        case 'text':
                        case 'number':
                        case 'file':
                            inputHtml = `<input id='role_field_${id}' type='${type}' data-id='${id}' ${isRequired} />`;
                            break;
                        case 'list':
                            let fieldListHtml = '';
                            listField = listField.split("|");

                            for (let j = 0; j < listField.length; j++) {
                                let value = listField[j].split("#")[0];
                                let key = listField[j].split("#")[1];

                                fieldListHtml += `<option value="${key}" >${value}</option>`;
                            }

                            inputHtml = `<select id='role_field_${id}' data-id='${id}' ${isRequired}>
                                            ${fieldListHtml}
                                        </select>`;
                            break;
                    }
                    html += `${labelHtml} ${inputHtml}`;
                }

                $('#roleForm').html(html);

                setTimeout(()=>{
                    fieldsValue.map(function(el){
                        let element = $('#roleForm > [data-id="'+el.field_id+'"]').first();
                        if(element){
                            element.prop('required',false);
                            if(element.attr('type') !== 'file'){
                                element.val(el.value);
                            }
                        }
                    });

                    this.validateForm(false);
                },200);
            }
        },
        mounted() {
            let self = this;

            axios.get(`/role`)
                .then((response) => {
                    this.roles = response.data;
                });

            $(document).off('change', '#roleForm > input[type="file"]');
            $(document).on('change', '#roleForm > input[type="file"]', function(){
                let reader = new FileReader();
                let el = $(this);
                reader.readAsDataURL($(this)[0].files[0]);
                reader.onload = function () {
                    el.data('value',reader.result);
                };
            });

            $(document).off('change', '#userRole');
            $(document).on('change', '#userRole', function () {
                axios.get(`/role/${$(this).val()}`)
                    .then((response) => {
                        self.role = response.data;
                        self.buildRoleForm();
                    })
            });

            if (this.isEdit) {
                this.getUser();
            }
        }
    }
</script>

<style>
    .badReq {
        border: 1px solid red;
    }
</style>


