<template>
    <div class="panel">
        <div class="panel-header">
            <h3>Logi systemowe</h3>
            <button class="panelActionTop" @click="removeLogs">Wyczyść logi</button>
        </div>
        <table class="sortable-theme-light" data-sortable>
            <thead>
                <tr>
                    <th>Data</th>
                    <th>Typ</th>
                    <th>Treść</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="log in logs">
                    <td>{{log.date}}</td>
                    <td style="font-weight: bold;" :class="{ warning: log.type==='WARNING', error: log.type==='ERROR' }">{{log.type}}</td>
                    <td >{{log.content}}</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    export default {
        name: "LogsPanel",
        data: function(){
            return {
                logs: [],
                limit: 10
            }
        },
        methods:{
            getLogs(){
                axios.get(`/logs?limit=${this.limit}`)
                    .then((response) => {
                        this.logs = response.data;
                        Sortable.init()
                    })
            },
            removeLogs(){
                axios.delete(`/logs`)
                    .then((response) => {
                        this.logs = [];
                        Sortable.init();
                    })
            }
        },
        mounted(){
            this.getLogs();
        }
    }
</script>

<style scoped>

</style>


