<template>
    <main>
        <div class="row">
            <div class="panel col wow fadeInUp" style="max-width: 500px; margin: auto">
                <div class="panel-header">
                    <h3>Dodawanie nowej licencji</h3>
                </div>
                <form>
                    <label for="licUser">Id użytkownika</label>
                    <input type="number" id="licUser" v-model="license.user_id" autofocus>

                    <label for="lic">Id licencji</label>
                    <input type="number" id="lic" v-model="license.license_id" >

                    <label for="licEnd">Data końca</label>
                    <input type="date" id="licEnd" v-model="license.date_end" >

                    <button v-if="!isEdit" @click="addLicense" :disabled="!isValidForm">Dodaj</button>
                    <button v-else @click="saveLicense" :disabled="!isValidForm">Zapisz</button>
                </form>
            </div>
        </div>
    </main>
</template>

<script>
    export default {
        name: "UserLicenseAdd",
        props: ['id'],
        data() {
            return {
                license: {
                    user_id: '',
                    license_id: '',
                    date_end: ''
                },
                isEdit: this.id !== 'add'
            }
        },
        methods: {
            formValid(showError = true) {
                try {
                    let valid = new window.Validator({
                        "id użytkownika": this.license.user_id,
                        "id licencji": this.license.license_id,
                        "data końca": this.license.date_end,
                    },showError);
                    valid.get("id użytkownika").length(1, 50);
                    valid.get("id licencji").length(1, 50);
                    valid.get("data końca").length(5, 50);
                } catch (e) {
                    return false;
                }
                return true;
            },
            addLicense() {
                if (!this.formValid()) return;

                axios.post(`/userLicense`, this.license)
                    .then((response) => {
                        this.$router.push('/license');
                    })
            },
            saveLicense() {
                if (!this.formValid()) return;

                axios.put(`/userLicense/${this.id}`, this.license)
                    .then((response) => {
                        this.$router.push('/license');
                    })
            }
        },
        computed:{
            isValidForm: function(){return this.formValid(false);}
        },
        mounted() {

            if (this.isEdit) {
                axios.get(`/userLicense/${this.id}`)
                    .then((response) => {
                        this.license = response.data;
                    })
            }

        }
    }
</script>

<style scoped lang="scss">
</style>


