<template>
    <div class="panel">
        <div class="panel-header">
            <h3>Ostatnio dodani użytkownicy</h3>
            <button class="panelActionTop" @click="$router.push('/user')">Zarządzaj</button>
        </div>
        <table class="sortable-theme-light" data-sortable>
            <thead>
            <tr>
                <th>ID</th>
                <th>IMG</th>
                <th>Imię / Nazwisko</th>
                <th>Data dołączenia</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="user in users">
                <td>{{user.id}}</td>
                <td><img class='profileImgMini' :src="user.profile_img" :alt="user.firstname"></td>
                <td>{{user.firstname}} {{user.lastname}}</td>
                <td>{{user.date_add}}</td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    export default {
        name: "LastUserPanel",
        data: function(){
            return {
                users: [],
                limit: 5
            }
        },
        methods:{
            getUsers(){
                axios.get(`/user?limit=${this.limit}&sort=date_add|desc`)
                    .then((response) => {
                        this.users = response.data;
                        Sortable.init();
                    })
            }
        },
        mounted(){
            this.getUsers();
        }
    }
</script>

<style scoped>

</style>


