<template>
    <main>
        <div class="row">
            <div class="panel col wow fadeInUp" style="max-width: 500px; margin: auto">
                <div class="panel-header">
                    <h3>Dodawanie nowej roli</h3>
                </div>
                <form>
                    <label for="roleName">Nazwa roli</label>
                    <input type="text" id="roleName" v-model="roleName" autofocus>


                    <label for="roleFormElement">Rozszerzone pola dla roli</label>
                    <transition name="fade" mode="out-in">
                        <div>
                            <p v-show="roleFormElements.length <= 0" style="text-align: center">
                                <small>Brak elementów. Możesz dodać, aby każdy użytkownik w tej grupie dysponował tymi
                                    polami.
                                </small>
                            </p>
                            <ul id="roleFormElement">
                                <li v-for="element in roleFormElements">{{element.name}}<i class="js-remove"
                                                                                           @click="removeFormField(element.id)">✖</i>
                                </li>
                            </ul>
                            <form v-show="fieldAdd" class="roleFormAddFieldForm">
                                <label for="FieldFormName">Nazwa pola</label>
                                <input type="text" id="FieldFormName" v-model="fieldForm.name" autofocus>
                                <label for="FieldFormType">Typ pola</label>
                                <select id="FieldFormType" v-model="fieldForm.type">
                                    <option value="number">Numer</option>
                                    <option value="list">Lista</option>
                                    <option value="text">Tekst</option>
                                    <option value="file">Plik</option>
                                </select>
                                <label v-show="fieldForm.type === 'list'" for="FieldFormList">Lista możliwości</label>
                                <input v-show="fieldForm.type === 'list'" placeholder="nazwa#wartosc|nazwa#wartosc"
                                       type="text" id="FieldFormList" v-model="fieldForm.list">

                                <label for="FieldFormRequired" style="margin-top: 15px; ">Pole jest wymagane?</label>
                                <input type="checkbox" id="FieldFormRequired" style="margin-bottom: 15px;" v-model="fieldForm.required">

                                <div style="display: flex; margin-top: 10px;">
                                    <button style="margin: 5px auto; display: block; line-height: 12px;"
                                            @click.prevent="cancelFormField">Anuluj
                                    </button>
                                    <button style="margin: 5px auto; display: block; line-height: 12px;"
                                            @click.prevent="endFormField">Dodaj
                                    </button>
                                </div>
                            </form>
                            <button v-show="!fieldAdd" style="margin: 5px auto; display: block; line-height: 12px;"
                                    @click.prevent="addFormField">Dodaj pole
                            </button>
                        </div>
                    </transition>

                    <button v-if="!isEdit" @click="addRole" :disabled="roleName.length < 3">Dodaj</button>
                    <button v-else @click="saveRole" :disabled="roleName.length < 3">Zapisz</button>
                </form>
            </div>
        </div>
    </main>
</template>

<script>
    export default {
        name: "RoleAdd",
        props: ['id'],
        data() {
            return {
                roleName: '',
                roleFormElements: [],
                fieldForm: {
                    name: '',
                    type: '',
                    list: '',
                    required: true
                },
                fieldAdd: false,
                isEdit: this.id !== 'add'
            }
        },
        methods: {
            endFormField() {
                try {
                    let valid = new window.Validator({
                        "nazwa pola": this.fieldForm.name,
                        "typ pola": this.fieldForm.type
                    });

                    valid.get("nazwa pola").length(3, 50);
                    valid.get("typ pola").length(3, 50);

                    if (this.fieldForm.type === 'list' && this.fieldForm.list.length <= 3) {
                        notify.createNotifier("Wpisz liste atrybutów do pola; lista", "danger", 4000);
                        throw "validate failed";
                    }

                } catch (e) {
                    return false;
                }

                this.roleFormElements.push($.extend({}, this.fieldForm));
                this.fieldAdd = false;
            },
            cancelFormField() {
                this.fieldAdd = false;
            },
            addFormField() {
                this.fieldForm = {
                    id: this.roleFormElements.length + 1,
                    name: '',
                    type: '',
                    list: '',
                    required: true
                };
                this.fieldAdd = true;
            },
            removeFormField(id) {
                this.roleFormElements = this.roleFormElements.filter((el, key) => {
                    return el.id !== id;
                })
            },
            validForm() {
                try {
                    let valid = new window.Validator({
                        "nazwa roli": this.roleName,
                    });
                    valid.get("nazwa roli").length(3, 50);
                } catch (e) {
                    return false;
                }
                return true;
            },
            addRole() {
                if (!this.validForm()) return;

                axios.post(`/role`, {
                    name: this.roleName,
                    fields: this.roleFormElements
                })
                    .then((response) => {
                        this.$router.push('/permission');
                    })
            },
            saveRole() {
                if (!this.validForm()) return;

                axios.put(`/role/${this.id}`, {
                    name: this.roleName,
                    fields: this.roleFormElements
                })
                    .then((response) => {
                        this.$router.push('/permission');
                    })
            }
        },
        mounted() {

            if (this.isEdit) {
                axios.get(`/role/${this.id}`)
                    .then((response) => {
                        this.roleName = response.data.name;
                        if(response.data.form_field){
                            this.roleFormElements = response.data.form_field;

                            this.roleFormElements.map((el) => {
                                el.name = el.label;
                                el.type = el.field_type;
                                el.list = el.list_field;
                            });
                        }

                    })
            }

            SortableJs.create($('#roleFormElement')[0]);

        }
    }
</script>

<style scoped lang="scss">

    form.roleFormAddFieldForm {
        padding: 15px;
        border: 1px solid #2d7dd2;
        border-radius: 5px;
    }

    ul#roleFormElement {
        list-style: none;
        margin: 15px 0;
        li {
            position: relative;
            text-align: center;
            background: #ffffff99;
            line-height: 30px;
            margin: 2px;
            cursor: move;
            transition: all .2s;
            &:hover {
                background: #abcbed;
            }

            i.js-remove {
                position: absolute;
                right: 10px;
                cursor: pointer;
                transition: all .2s;
                &:hover {
                    transform: scale(1.1);
                }
            }
        }
    }
</style>


