<template>
    <div class="panel">
        <div class="panel-header">
            <h3>Statystyki dotyczące tabel</h3>
            <button class="panelActionTop" @click="$emit('refresh')">Odśwież status</button>
        </div>
        <table class="sortable-theme-light" data-sortable>
            <thead>
            <tr>
                <th>Nazwa</th>
                <th data-sortable-type="numeric">Ilość rekordów</th>
                <th data-sortable-type="numeric">Rozmiar</th>
                <th data-sortable="false">Czyszczenie</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="table in tables">
                <td>{{table.TABLE_NAME}}</td>
                <td>{{table.TABLE_ROWS}}</td>
                <td>{{table.DATA_LENGTH}} mb</td>
                <td>
                    <div @click='deleteTableRow(table.TABLE_NAME)'>
                        <i class="btn-i fas fa-trash"></i>
                    </div>
                </td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    export default {
        name: "TableStatPanel",
        props: [
            "tables"
        ],
        methods:{
            deleteTableRow(tableName){
                axios.delete(`/database/row/${tableName}`)
                    .then((response) => {
                        this.$emit('refresh');
                    });
            }
        },
        watch: {
            tables: function() {
                Sortable.init();
            }
        }
    }
</script>

<style scoped>

</style>


