<template>
    <div class="panel">
        <div class="panel-header">
            <h3>Dostępne uprawnienia</h3>
            <button class="panelActionTop" @click="$router.push('/permission/add')">Dodaj</button>
        </div>
        <table class="sortable-theme-light" data-sortable>
            <thead>
            <tr>
                <th>ID</th>
                <th>Nazwa</th>
                <th>Akcje</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="perm in permission">
                <td>{{perm.id}}</td>
                <td>{{perm.name}}</td>
                <td class="action">
                    <div @click='editPermission(perm.id)'>
                        <i class="btn-i fas fa-edit"></i>
                    </div>
                    <div @click='deletePermission(perm.id)'>
                        <i class="btn-i fas fa-trash"></i>
                    </div>
                </td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    export default {
        name: "PermissionPanel",
        data: () => (
            {
                permission: []
            }
        ),
        methods: {
            getPermission(){
                axios.get(`/permission`)
                    .then((response) => {
                        this.permission = response.data;
                    })
            },
            editPermission(id){
                this.$router.push(`/permission/${id}`);
            },
            deletePermission(id){
                axios.delete(`/permission/${id}`)
                    .then((response) => {
                        this.getPermission();
                    })
            }
        },
        mounted: function(){
            this.getPermission();
        }
    }
</script>

<style scoped>

</style>


