<template>
    <main>
        <div class="row">
            <div class="panel col wow fadeInUp" style="max-width: 500px; margin: auto">
                <div class="panel-header">
                    <h3>Dodawanie nowej licencji</h3>
                </div>
                <form>
                    <label for="licName">Nazwa licencji</label>
                    <input type="text" id="licName" v-model="license.name" autofocus>

                    <label for="licDescription">Opis</label>
                    <textarea id="licDescription" placeholder="Opis licencji" v-model="license.description" />

                    <label for="licPrice">Ceny licencji</label>
                    <input type="text" id="licPrice" placeholder="l.miesiecy#cena|l.miesiecy#cena" v-model="license.price" >

                    <label for="licPowers">Możliwości</label>
                    <textarea id="licPowers" placeholder="power|power|power" v-model="license.powers" />

                    <label for="licRole">Możliwe role</label>
                    <textarea id="licRole" placeholder="idRoli|idRoli|idRoli" v-model="license.possibleRole" />

                    <label for="licDomain">Możliwe domeny</label>
                    <textarea id="licDomain" placeholder="domain|domain|domain" v-model="license.possibleDomain" />

                    <button v-if="!isEdit" @click="addRole" :disabled="!isValidForm">Dodaj</button>
                    <button v-else @click="saveRole" :disabled="!isValidForm">Zapisz</button>
                </form>
            </div>
        </div>
    </main>
</template>

<script>
    export default {
        name: "LicenseAdd",
        props: ['id'],
        data() {
            return {
                license: {
                    name: '',
                    description: '',
                    possibleDomain: '',
                    possibleRole: '',
                    powers: '',
                    price: null
                },
                isEdit: this.id !== 'add'
            }
        },
        methods: {
            formValid(showError = true) {
                try {
                    let valid = new window.Validator({
                        "nazwa licencji": this.license.name,
                        "opis": this.license.description,
                        "możliwe domeny": this.license.possibleDomain,
                        "możliwe role": this.license.possibleRole,
                        "możliwości licencji": this.license.powers,
                        "cena licencji": this.license.price,
                    },showError);
                    valid.get("nazwa licencji").length(3, 255);
                    valid.get("opis").length(3, 500);
                    valid.get("możliwe domeny").length(3, 500);
                    valid.get("możliwe role").length(1, 500);
                    valid.get("możliwości licencji").length(3, 500);
                    valid.get("cena licencji").length(3, 255);
                } catch (e) {
                    return false;
                }
                return true;
            },
            addRole() {
                if (!this.formValid()) return;

                axios.post(`/license`, this.license)
                    .then((response) => {
                        this.$router.push('/license');
                    })
            },
            saveRole() {
                if (!this.formValid()) return;

                axios.put(`/license/${this.id}`, this.license)
                    .then((response) => {
                        this.$router.push('/license');
                    })
            }
        },
        computed:{
          isValidForm: function(){return this.formValid(false);}
        },
        mounted() {

            if (this.isEdit) {
                axios.get(`/license/${this.id}`)
                    .then((response) => {
                        this.license = response.data;
                    })
            }

        }
    }
</script>

<style scoped lang="scss">
</style>


