<template>
    <div class="panel">
        <div class="panel-header">
            <h3>Licencje powiązane</h3>
            <button class="panelActionTop" @click="$router.push('/userLicense/add')">Dodaj</button>
        </div>
        <table class="sortable-theme-light" data-sortable>
            <thead>
            <tr>
                <th>Użytkownik</th>
                <th>Licencja</th>
                <th>Data końca</th>
                <th>Akcje</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="license in licenses">
                <td>{{license.user.firstname}} {{license.user.lastname}}</td>
                <td>{{license.license.name}}</td>
                <td>{{license.date_end}}</td>
                <td class="action">
                    <div @click='editLicense(license.id)'>
                        <i class="btn-i fas fa-edit"></i>
                    </div>
                    <div @click='deleteLicense(license.id)'>
                        <i class="btn-i fas fa-trash"></i>
                    </div>
                </td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    export default {
        name: "UserLicensePanel",
        data: () => (
            {
                licenses: []
            }
        ),
        methods: {
            getLicense(){
                axios.get(`/userLicense`)
                    .then((response) => {
                        this.licenses = response.data;
                    })
            },
            editLicense(id){
                this.$router.push(`/userLicense/${id}`);
            },
            deleteLicense(id){
                axios.delete(`/userLicense/${id}`)
                    .then((response) => {
                        this.getLicense();
                    })
            }
        },
        mounted: function(){
            this.getLicense();
        }
    }
</script>

<style scoped>

</style>


